<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-lembur', data)" class="btn btn-info btn-sm">Lihat Detail</a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData.detail" id="modal-rincian-lembur" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="text-center mb-5">
                <h5 class="mb-1 font-weight-bold">Detail {{ config.title }}</h5>
                <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">MPP</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.is_mpp | parse('ynBool') }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <template v-if="modalData.detail">
          <b-tabs>
            <b-tab title="Penerimaan Tunjangan" active>
              <div class="row py-5">
                <div class="col-md-6 d-flex justify-content-between flex-column">
                  <table class="table no-border">
                    <tr>
                      <td class="pt-1 pl-0 nowrap-table">Uang Makan &amp; Transport</td>
                      <td class="pt-1 pl-0 nowrap-table">:</td>
                      <th class="pt-1 ">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.&emsp;</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail[detailTabs[3].point].map(x => x.component_value).reduce((acc, curr) => acc + curr, 0) | parse('number') }}</span>
                        </div>
                      </th>
                    </tr>
                    <tr>
                      <td class="pt-1 pl-0 nowrap-table">Uang Lembur Hari Kerja ({{ modalData.detail[detailTabs[1].point].map(x => x.jam_lembur_count).reduce((acc, curr) => acc + curr, 0) | parse('number') }} Jam)</td>
                      <td class="pt-1 pl-0 nowrap-table">:</td>
                      <th class="pt-1 ">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.&emsp;</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail[detailTabs[1].point].map(x => x.penerimaan_value).reduce((acc, curr) => acc + curr, 0) | parse('number') }}</span>
                        </div>
                      </th>
                    </tr>
                    <tr>
                      <td class="pt-1 pl-0 nowrap-table">Uang Lembur Hari Libur ({{ modalData.detail[detailTabs[2].point].map(x => x.jam_lembur_count).reduce((acc, curr) => acc + curr, 0) | parse('number') }} Jam)</td>
                      <td class="pt-1 pl-0 nowrap-table">:</td>
                      <th class="pt-1 ">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.&emsp;</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail[detailTabs[2].point].map(x => x.penerimaan_value).reduce((acc, curr) => acc + curr, 0) | parse('number') }}</span>
                        </div>
                      </th>
                    </tr>
                    <tr>
                      <td class="pl-0 nowrap-table" style="border-top: 2px solid #000">Penerimaan Tunjangan Lembur</td>
                      <td class="pl-0 nowrap-table" style="border-top: 2px solid #000">:</td>
                      <th class="" style="border-top: 2px solid #000">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.&emsp;</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.penerimaan_value || 0 | parse('number') }}</span>
                        </div>
                      </th>
                    </tr>
                    <tr>
                      <td class="pl-0 nowrap-table">Pembulatan</td>
                      <td class="pl-0 nowrap-table">:</td>
                      <th class="">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.&emsp;</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.pembulatan_penerimaan_value || 0 | parse('number') }}</span>
                        </div>
                      </th>
                    </tr>
                  </table>
                  <div class="mt-5">
                    <button @click="submitData('modal-rincian-lembur')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="alert alert-custom alert-default mb-0" role="alert">
                    <div class="alert-text">
                      <h5 class="alert-heading">Dasar perhitungan uang lembur</h5>
                      <p class="mb-0">Keputusan Mentri Tenaga Kerja dan Transmigrasi Republik Indonesia nomor KEP.102/MEN/VI/2004 tentang waktu kerja lembur dan upah lembur</p>
                      <ul class="pl-5 mb-0">
                        <li>Dalam hal upah terdiri dari upah pokok dan tunjangan tetap, maka dasar perhitungan upah lembur adalah <b>100%</b> dari upah</li>
                        <li>Dalam hal upah terdiri dari upah pokok, tunjangan tetap, dan tunjangan tidak tetap. Apabila upah pokok ditambah tunjangan tetap lebih kecil dari 75% keseluruhan upah, maka dasar perhitungan upah lembur 75% dari keseluruhan upah</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </b-tab>
            <b-tab title="Upah Lembur Perjam">
              <div class="py-5">
                <table class="table table-custom-header v-center table-bordered">
                  <thead>
                    <tr>
                      <th>Nama Komponen</th>
                      <th>Jenis Komponen</th>
                      <th class="nowrap-table">Koefisien</th>
                      <th class="text-center">Nilai Dasar</th>
                      <th class="text-center">Nilai Komponen</th>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-for="(komp,i) in modalData.detail[detailTabs[0].point]">
                      <tr :key="i+'-body-komp'">
                        <td class="nowrap-table">{{ komp.rel_salary_component_id }}</td>
                        <td class="nowrap-table">{{ komp.type | parse('status_code') }}</td>
                        <td class="bg-light-primary">
                          <s-input v-model="komp.koefisien" :item="{ type:'number', validation:[], label:null, prefix: null, groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:0 } }" />
                        </td>
                        <td class="bg-light-primary">
                          <s-input v-model="komp.default_value" :item="{ type:'number', validation:[], label:null, prefix: 'Rp. ', groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:2 } }" />
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ (komp.koefisien * komp.default_value) || 0 | parse('number') }}</span>
                          </div>
                        </td>
                      </tr>
                    </template>
                  </tbody>
                </table>
                <p>
                  <span class="label label-inline label-pill label-danger label-rounded mr-2">NOTE</span>Kembali ke tab <span class="text-dark-75 font-weight-bolder">Penerimaan Tunjangan</span> untuk memperbarui perhitungan
                </p>
                <div class="row">
                  <div class="col-md-4">
                    <table class="table no-border mt-5">
                      <tr>
                        <td class="pt-1 pl-0 nowrap-table">Nilai Upah (THP)</td>
                        <td class="pt-1 pl-0 nowrap-table">:</td>
                        <th class="pt-1 ">
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.&emsp;</span>
                            <span class="text-right font-weight-bold">{{ modalData.detail.upah_value || 0 | parse('number') }}</span>
                          </div>
                        </th>
                      </tr>
                      <tr>
                        <td class="pt-1 pl-0 nowrap-table">Nilai Pokok + Tunjangan Tetap</td>
                        <td class="pt-1 pl-0 nowrap-table">:</td>
                        <th class="pt-1 ">
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.&emsp;</span>
                            <span class="text-right font-weight-bold">{{ modalData.detail.pokok_tunjangan_tetap_value || 0 | parse('number') }}</span>
                          </div>
                        </th>
                      </tr>
                      <tr>
                        <td class="pt-1 pl-0 nowrap-table">Nilai 75% THP</td>
                        <td class="pt-1 pl-0 nowrap-table">:</td>
                        <th class="pt-1 ">
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.&emsp;</span>
                            <span class="text-right font-weight-bold">{{ modalData.detail.three_quarter_upah_value || 0 | parse('number') }}</span>
                          </div>
                        </th>
                      </tr>
                      <tr>
                        <td class="pl-0 nowrap-table" style="border-top: 2px solid #000">Nilai Uang Lembur Perjam</td>
                        <td class="pl-0 nowrap-table" style="border-top: 2px solid #000">:</td>
                        <th class="" style="border-top: 2px solid #000">
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.&emsp;</span>
                            <span class="text-right font-weight-bold">{{ modalData.detail.upah_lembur_perjam_value || 0 | parse('number') }}</span>
                          </div>
                        </th>
                      </tr>
                    </table>
                  </div>
                  <div class="col-md-8">
                    <div class="py-3 px-5 border-primary mt-2 bg-light-primary" style="border:2px solid;border-radius:8px">
                      <h5 class="font-weight-bolder text-primary">Informasi Perhitungan</h5>
                      <ul class="font-size-sm pl-5">
                        <li>Koefisien upah lembur perjam : <code>1/173</code></li>
                        <li>Rumus uang lembur perjam: <br> Jika Nilai Tunjangan Tetap + Pokok &#60; Nilai 75% THP :<code>75% * Nilai Upah (THP) * 1/173</code><br> Jika Nilai Tunjangan Tetap + Pokok &ge; Nilai 75% THP :<code>Nilai Upah (THP) x 1/173</code>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </b-tab>
            <b-tab title="Subsidi Lembur">
              <div class="pt-5">
                <table class="table table-custom-header v-center table-bordered">
                  <thead>
                    <tr>
                      <th>Nama Komponen</th>
                      <th class="text-center">Jumlah hari Lembur</th>
                      <th class="nowrap-table">Koefisien</th>
                      <th class="text-center">Nilai Dasar</th>
                      <th class="text-center">Nilai Komponen</th>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-for="(komp,i) in modalData.detail[detailTabs[3].point]">
                      <tr :key="i+'-body-komp'">
                        <td class="nowrap-table">{{ komp.rel_salary_component_id }}</td>
                        <td class="bg-light-primary">
                          <s-input v-model="komp.jumlah_hari" :item="{ type:'number', validation:[], label:null, prefix: null, groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:1 } }" />
                        </td>
                        <td class="bg-light-primary">
                          <s-input v-model="komp.koefisien" :item="{ type:'number', validation:[], label:null, prefix: null, groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:1 } }" />
                        </td>
                        <td class="bg-light-primary">
                          <s-input v-model="komp.default_value" :item="{ type:'number', validation:[], label:null, prefix: 'Rp. ', groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:2 } }" />
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ ( komp.jumlah_hari * komp.koefisien * komp.default_value) || 0 | parse('number') }}</span>
                          </div>
                        </td>
                      </tr>
                    </template>
                    <tr>
                      <th class="text-right" colspan="4">Total Penerimaan</th>
                      <td class="">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail[detailTabs[3].point].map(x => x.component_value).reduce((acc, curr) => acc + curr, 0) | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <p>
                <span class="label label-inline label-pill label-danger label-rounded mr-2">NOTE</span>Kembali ke tab <span class="text-dark-75 font-weight-bolder">Penerimaan Tunjangan</span> untuk memperbarui perhitungan
              </p>
              <div class="py-3 px-5 border-primary mt-2 bg-light-primary" style="border:2px solid;border-radius:8px">
                <h5 class="font-weight-bolder text-primary">Informasi Perhitungan</h5>
                <ul class="font-size-sm pl-5">
                  <li>Koefisien didapat dari aturan berikut: <code>Pegawai Tetap </code> yang melaksanakan kerja lembur selain diberikan upah lembur diberikan uang makan dan uang transport sebagai berikut: Apabila ditugaskan <code> lembur melebihi 4 Jam terus menerus di luar hari kerja normal </code> atau <code>melebihi 2,5 jam terus menerus pada hari kerja normal </code> diberikan <code>1,5</code> Kali tunjangan makan</li>
                  <li>Jumlah Hari Lembur: <code>Jumlah Lembur Hari Kerja + Jumlah Lembur Hari Libur</code></li>
                  <li>Nilai Komponen: <code>Koefisien x Jumlah Hari Lembur x Nilai Dasar Tunjangan</code></li>
                </ul>
              </div>
            </b-tab>
            <b-tab title="Lembur Hari Kerja">
              <div class="table-responsive pt-5">
                <table class="table table-custom-header v-center table-bordered">
                  <thead>
                    <tr>
                      <th rowspan="2">Tanggal Lembur</th>
                      <th rowspan="2" class="nowrap-table text-center">Durasi</th>
                      <th colspan="2" class="text-center">Nilai Uang Lembur</th>
                      <th rowspan="2" class="text-center">Jumlah</th>
                    </tr>
                    <tr>
                      <th class="text-center">Jam Pertama</th>
                      <th class="text-center">Jam Berikutnya</th>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-for="(komp,i) in modalData.detail[detailTabs[1].point]">
                      <tr :key="i+'-body-komp'">
                        <td class="nowrap-table">{{ komp.date | parse('longDate') }}</td>
                        <td class="nowrap-table">{{ komp.jam_lembur_count | parse('number') }} Jam</td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.lembur_jam_pertama_value | parse('number') }}</span>
                          </div>
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.lembur_jam_selanjutnya_value | parse('number') }}</span>
                          </div>
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.penerimaan_value | parse('number') }}</span>
                          </div>
                        </td>
                      </tr>
                    </template>
                    <tr>
                      <th class="text-right bg-light-primary" colspan="4">Total Penerimaan</th>
                      <td class="bg-light-primary">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail[detailTabs[1].point].map(x => x.penerimaan_value).reduce((acc, curr) => acc + curr, 0) | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="py-3 px-5 border-primary mt-2 bg-light-primary" style="border:2px solid;border-radius:8px">
                <h5 class="font-weight-bolder text-primary">Informasi Perhitungan</h5>
                <ul class="font-size-sm pl-5">
                  <li>Koefisien upah lembur Jam Pertama : <code>1,5</code></li>
                  <li>Rumus Uang lembur Jam Pertama: <code>Uang Lembur Perjam x Koefisien</code></li>
                  <li>Rumus Uang lembur Jam Selanjutnya: <code>(Jam Lembur - 1) x Uang Lembur Perjam x 2</code></li>
                </ul>
              </div>
            </b-tab>
            <b-tab title="Lembur Hari Libur">
              <div class="table-responsive pt-5">
                <table class="table table-custom-header v-center table-bordered">
                  <thead>
                    <tr>
                      <th rowspan="2">Tanggal Lembur</th>
                      <th rowspan="2" class="nowrap-table text-center">Durasi</th>
                      <th colspan="3" class="text-center">Nilai Uang Lembur</th>
                      <th rowspan="2" class="text-center">Jumlah</th>
                    </tr>
                    <tr>
                      <th class="text-center">8 Jam Pertama</th>
                      <th class="text-center">Jam ke-9</th>
                      <th class="text-center">Jam selanjutnya</th>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-for="(komp,i) in modalData.detail[detailTabs[2].point]">
                      <tr :key="i+'-body-komp'">
                        <td class="nowrap-table">{{ komp.date | parse('longDate') }}</td>
                        <td class="nowrap-table">{{ komp.jam_lembur_count | parse('number') }} Jam</td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.lembur_jam_kedelapan_value | parse('number') }}</span>
                          </div>
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.lembur_jam_kesembilan_value | parse('number') }}</span>
                          </div>
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.lembur_jam_selanjutnya_value | parse('number') }}</span>
                          </div>
                        </td>
                        <td>
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ komp.penerimaan_value | parse('number') }}</span>
                          </div>
                        </td>
                      </tr>
                    </template>
                    <tr>
                      <th class="text-right bg-light-primary" colspan="5">Total Penerimaan</th>
                      <td class="bg-light-primary">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail[detailTabs[2].point].map(x => x.penerimaan_value).reduce((acc, curr) => acc + curr, 0) | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="py-3 px-5 border-primary mt-2 bg-light-primary" style="border:2px solid;border-radius:8px">
                <h5 class="font-weight-bolder text-primary">Informasi Perhitungan</h5>
                <ul class="font-size-sm pl-5">
                  <li>Rumus Uang lembur 8 Jam Pertama: <code>Uang Lembur Perjam x Jam Lembur x 2</code></li>
                  <li>Rumus Uang lembur Jam ke 9: <code>Uang Lembur Perjam x 1 Jam x 3</code></li>
                  <li>Rumus Uang lembur Jam Selanjutnya: <code>Uang Lembur Perjam x (Jam Lembur - 9) x 4</code></li>
                </ul>
              </div>
            </b-tab>
          </b-tabs>
        </template>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-tunjangan-lembur-details',
  data () {
    return {
      detailTabs: [
        { name: 'Komponen Perhitungan', point: 'child_data_tunjangan_lembur_upah_components' },
        { name: 'Data Lembur Hari Kerja', point: 'child_data_tunjangan_lembur_hari_kerja' },
        { name: 'Data Lembur Hari Libur', point: 'child_data_tunjangan_lembur_hari_libur' },
        { name: 'Uang Makan & Transport', point: 'child_data_tunjangan_lembur_subsidiary' }
      ],
      modalData: {},
      mainKey: 0,
      config: {
        title: 'Data Tunjangan Lembur',
        model_api: null,
        getter: 'custom/tunjangan-lembur-details',
        setter: 'tunjangan-lembur-details',
        pk_field: 'name',
        filter_api: {},
        custom_api: {
          create: 'custom/tunjangan-lembur-details'
        },
        permission: {
          create: 'create-tunjangan-lembur-details',
          read: 'view-tunjangan-lembur-details',
          update: 'update-tunjangan-lembur-details',
          delete: 'delete-tunjangan-lembur-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            label: 'Status',
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              create: false,
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id', show: false },
              create: false,
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          },
          { id: 'penerimaan_value', label: 'Penerimaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'upah_lembur_perjam_value', label: 'Upah Lembur/Jam', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          //   { id: 'tunjangan_tetap_value', label: 'Tunjangan Tetap', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          //   { id: 'upah_value', label: 'Upah', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          //   { id: 'three_quarter_upah_value', label: 'Upah 3/4', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        try {
          this.$refs['sub-module'].$children[1].getData()
        } catch (error) {
          this.$refs['sub-module'].$children[2].getData()
        }
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('get', 'custom/tunjangan-lembur-details/show', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    copyValue (obj = null) {
      if (!obj) {
        this.$set(this.modalData, 'detail', { ...this.modalData.detail, ...this.modalData.sync.data_recaps })
      }
    },
    submitData () {
      const api = this.config.getter + '/calculate'
      this.$_api.post(api, this.modalData.detail).then(res => {
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
        this.$set(this.modalData, 'detail', res.data)
        this.mainKey++
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



